body {
    margin: 0;
    background-color: #ffffff;
    color: #000000;
    overflow-x: hidden;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html {
    font-size: 62.5%;
    padding: 0;
    overflow-x: hidden;
    margin: 0;
    scroll-behavior: smooth;
}

* {
    box-sizing: border-box;
}

picture {
    margin: 0;
    padding: 0;
    font-size: 0;
}

input,
textarea,
button,
select,
a {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 300;
    font-display: swap;
    src: local('Lato Light'), local('Lato-Light'), url('/fonts/lato-v14-latin-300.woff2') format('woff2'), url('/fonts/lato-v14-latin-300.woff') format('woff');
}

@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: local('Lato Bold'), local('Lato-Bold'), url('/fonts/lato-v14-latin-700.woff2') format('woff2'), url('/fonts/lato-v14-latin-700.woff') format('woff');
}

@font-face {
